.jumbotron {
  width: 100%;
  margin-bottom: 20px;
  color: #013e8d;
  background-color: white;
  border-radius: 0;
}

.jumbotron .sub-heading {
  color: white;
}

#about .jumbotron, 
#contact .jumbotron {
  width: 100%;
  margin-top: 45px;
  padding: 10px;
  background-image: -webkit-gradient(linear,left bottom, left top,from(#18ABFB), to(#0060DF) );
  background-image: -webkit-linear-gradient(bottom,#18ABFB 0%, #0060DF 100% );
  background-image: -o-linear-gradient(bottom,#18ABFB 0%, #0060DF 100% );
  background-image: linear-gradient(to top,#18ABFB 0%, #0060DF 100% );
  color: white;
  border-radius: 0;
}

#about .jumbotron .sub-heading {
  color: white !important;
}




