table {
    width: 105.2%;
    margin-left: -30px;
    background-color:#0060df !important;
    text-align: center;
}

table, th, td {
    border: 1px solid  rgba(128, 128, 128, 0.7);
 }

th {
    background-color: #0060DF;
    color: white;
    text-align: center;
}

tr:hover {
    border: 2px solid #0060DF;
}

tr:nth-of-type(even) {
    background-color: rgba(0, 97, 223, 0.3);
}

table tbody {
	height: 562px;
	overflow-y: auto;
	width: 100%;
}

table thead tr{
	width: 99%;
}

 table tr{
 	width: 100%;
 }


table thead{
	background: #0060DF;
	color: #fff;
}

table tbody td, table thead > tr> th{
	float: left;
	border-bottom-width: 0;
}

table>tbody>tr>td, table>tbody>tr>th, table>tfoot>tr>td, table>tfoot>tr>th, table>thead>tr>td, table>thead>tr>th{
	padding: 8px;
	height: 50px;
	text-align: center;
	line-height: 32px;
}

.table_scrollbar{
	height: 612px;
	overflow-y: scroll;
}

.table_scrollbar::-webkit-scrollbar-track{
	-webkit-box-shadow: inset 0 0 6px #0060DF;
	border-radius: 10px;
	background-color: #444444;
}

.table_scrollbar::-webkit-scrollbar{
	width: 1%;
	min-width: 5px;
	background-color: #F5F5F5;
}

.table_scrollbar::-webkit-scrollbar-thumb{
	border-radius: 10px;
	background-color: #0060DF;
	background-image: -webkit-linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.4) 50%, transparent, transparent)
}

@media (max-width: 736px) {
	.table {
	  width: 100%;
	  -webkit-overflow-scrolling: auto;
	}
  }


  


